function EventParameter(event) {
    var i = 0;
    this.param = [];
    this.enable = false;

    if (event) {
        this.enable = true;
        for (i = 0; i < event.length; i += 1) {
            this.param.push(parseInt(event[i], 10));
        }
    }
}

function EventTypeFilter() {
    this.enable = false;
    this.error = null;
    this.profil = null;
    this.usage = null;
}

EventTypeFilter.prototype.setFilter = function(type, eventParameter) {
    this[type] = eventParameter;
};

EventTypeFilter.prototype.isEnabled = function() {
    return this.enable;
};

function Prisme(database, parameters) {
    AbstractFormatter.call(this, database);

    var eventsObjectFilterParam = (parameters.eventsObjectFilter && (parameters.eventsObjectFilter.length > 0)) ? parameters.eventsObjectFilter : this.DEFAULT_EVENTS_OBJECT_FILTER,
        eventTypeSessionFilterParam = (parameters.eventTypeSessionFilter && (parameters.eventTypeSessionFilter.length > 0)) ? parameters.eventTypeSessionFilter : this.DEFAULT_EVENT_TYPE_SESSION_FILTER,
        eventTypeRealTimeFilterParam = (parameters.eventTypeRealTimeFilter && (parameters.eventTypeRealTimeFilter.length > 0)) ? parameters.eventTypeRealTimeFilter : this.DEFAULT_EVENT_TYPE_REALTIME_FILTER,
        eventsObject = eventsObjectFilterParam.split(';');

    this.eventTypeSessionFilter = this.parseEventsFilter(eventTypeSessionFilterParam);
    this.eventTypeRealTimeFilter = this.parseEventsFilter(eventTypeRealTimeFilterParam);

    this.eventTypeSessionFilter.enable = eventsObject.indexOf('session') > -1 ? true : false;
    this.eventTypeRealTimeFilter.enable = eventsObject.indexOf('realtime') > -1 ? true : false;
}

Prisme.prototype = Object.create(AbstractFormatter.prototype);
Prisme.prototype.constructor = Prisme;

Prisme.prototype.parseEventsFilter = function(eventsFilter) {
    var eventTypeFilter = new EventTypeFilter(),
        //parse units events => error, usage and profil
        events = eventsFilter.split(';'),
        parsedElements = [],
        i = 0,
        j = 0,
        tabTemp;

    //parse for each unit events, numbers of elements to return (n first, and m last)
    for (i = 0; i < events.length; i += 1) {
        tabTemp = events[i].split(',');
        parsedElements[tabTemp[0]] = [];
        for (j = 1; j < tabTemp.length; j += 1) {
            parsedElements[tabTemp[0]].push(tabTemp[j]);
        }
    }

    //set, for each type filter, the different parameters : enable, n first and m last elements.
    eventTypeFilter.setFilter('error', new EventParameter(parsedElements.error));
    eventTypeFilter.setFilter('profil', new EventParameter(parsedElements.profil));
    eventTypeFilter.setFilter('usage', new EventParameter(parsedElements.usage));

    return eventTypeFilter;
};

Prisme.prototype.init = function() {
    AbstractFormatter.prototype.init.call(this);
    this.duration = 0;
    this.isInitSessionSend = false;
    this.currentBitrate = 0;
};

Prisme.prototype.generateSessionId = function() {
    return AbstractFormatter.prototype.generateSessionId.call(this, '-');
};

Prisme.prototype.process = function(metric) {
    var formattedData = null;

    if (!this.database) {
        return formattedData;
    }

    if (!metric) {
        formattedData = this.formatterRecurring();
    } else {
        if (metric.hasOwnProperty('metadata') && metric.metadata.duration !== undefined) {
            this.duration = metric.metadata.duration;
        }//if session.startBufferingTime and session.startPlayingTime are undefined, do not send session metric.
        if (metric.hasOwnProperty('session') && metric.session.startBufferingTime && metric.session.startPlayingTime) {
            formattedData = this.formatterSession();
        } // error is defined in eventTypeRealTimeFilter ? error type code also defined?
        else if (metric.hasOwnProperty('error')) {
            formattedData = this.formatterRealTime('/has/realtime/error/');
        } //use is defined in eventTypeRealTimeFilter ?
        else if ((metric.hasOwnProperty('action') && (this.eventTypeRealTimeFilter.usage.enable === true))) {
            formattedData = this.formatterRealTime('/has/realtime/use/');
        } // profil is defined in eventTypeRealTimeFilter ?
        else if ((metric.hasOwnProperty('encoding') && metric.encoding.hasOwnProperty('contentType') && metric.encoding.contentType === 'video' && (this.eventTypeRealTimeFilter.profil.enable === true))) {
            formattedData = this.formatterRealTime('/has/realtime/profil/');
        } else if (metric.hasOwnProperty('state')/* && this.firstAccess === false*/) {
            if (metric.state.current === 'stopped' && metric.state.previousState !== 'init') {
                formattedData = this.formatterSession();
            } //For Prisme, stalled state is an error
            else if (metric.state.current === 'buffering') {
                formattedData = this.formatterRealTime('/has/realtime/error/', 'buffering');
            } else if (metric.state.current === 'playing' && !this.isInitSessionSend) {
                formattedData = this.formatterSession();
                this.isInitSessionSend = true;
            }
        }
    }

    return formattedData;
};

