Prisme.prototype.formatSessionObject = function(excludedList) {
    var session = this.database.getLastMetricValue('session'),
        metadata = this.database.getLastMetricValue('metadata', 'video'),
        state,
        REGX_URI_PARAMETERS = /(\?|\&)([^=]+)\=([^&]+)/g,
        manifestParametersAndValue;

    if (session === null) {
        return {};
    }

    this.data = {};
    this.excludedList = excludedList;

    this.setFieldValue('clientSessionId', session.id);
    this.setFieldValue('playerId', session.playerid);
    this.setFieldValue('uuid', undefined);
    this.setFieldValue('url', session.uri);
    this.setFieldValue('userAgent', session.userAgent);
    this.setFieldValue('contentId', undefined);
    if (session.minBitrate) {
        this.setFieldValue('minBitrate', session.minBitrate / 1000);
    }
    if (session.maxBitrate) {
        this.setFieldValue('maxBitrate', session.maxBitrate / 1000);
    }
    this.setFieldValue('startLaunchDate', session.startTime);
    this.setFieldValue('startBufferingDate', session.startBufferingTime);
    this.setFieldValue('watchStartDate', session.startPlayingTime);
    while ((manifestParametersAndValue = REGX_URI_PARAMETERS.exec(session.uri)) !== null) {
        if (manifestParametersAndValue[2] === 'SID') {
            this.setFieldValue('pfsSessionId', manifestParametersAndValue[3]);
            break;
        }
    }
    //TBD uuid, contentName, httpBitrate

    this.setFieldValue('status', 'IN_PROGRESS');
    if (this.firstAccess === true) {
        this.firstAccess = false;
    } else {
        state = this.database.getLastMetricValue('state');
        if (state !== null) {
            if (state.current === 'stopped') {
                if (state.reason === 2) {
                    this.setFieldValue('status', 'KO');
                } else {
                    this.setFieldValue('status', 'OK');
                }
                // Set max position only for on-demand contents
                if (metadata && metadata.duration >= 0) {
                    this.setFieldValue('maxPosition', (session.maxPosition !== undefined) ? this.formatPositionValue(session.maxPosition) : 0);
                }
            }
        }
    }
    this.setFieldValue('watchEndDate', new Date().getTime());
    if (metadata === null) {
        return this.data;
    }

    if (metadata.duration >= 0 && metadata.duration !== Infinity) {
        this.setFieldValue('contentDuration', metadata.duration);
    }

    this.setFieldValue('userData', session.userData);

    return this.data;
};

