function OnePlayerCollector(player, video, database) {
    this.player = player;
    this.video = video;
    if (!this.video) {
        throw new Error('Unable to acces video HTML5 element');
    }
    this.database = database;
    this.sessionId = null;
    this.playerId = String(Math.random()).substring(2);

    this.onMetadataLoadedListener = this.onMetadataLoaded.bind(this);
    this.playbackEventListener = this.onPlaybackEvent.bind(this);
    this.timeupdateListener = this.onTimeupdate.bind(this);
    this.errorListener = this.onError.bind(this);
    this.qualityChangeRenderedListener = this.onQualityChangeRendered.bind(this);
    this.teardownListener = this.onTeardown.bind(this);

    // Map video element events to actions
    this.playbackActions = [];
    this.playbackActions.pause = 'pause';
    this.playbackActions.seeking = 'seek';
    this.playbackActions.play = 'play';

    // Map video element events to states
    this.playbackStates = [];
    this.playbackStates.ended = 'stopped';
    this.playbackStates.pause = 'paused';
    this.playbackStates.playing = 'playing';
    this.playbackStates.seeking = 'seeking';    
    this.playbackStates.waiting = 'buffering';    
}

OnePlayerCollector.prototype.init = function(sessionId, stream) {
    this.sessionId = sessionId;

    // Add new session metric
    this.addSession(stream);
    this.addState('startup');
};

OnePlayerCollector.prototype.stop = function() {
    try {
        var reason = this.database.getLastMetricValue('error') ? 2 : 0;
        this.addState('stopped', reason);
    } catch(ex) {
        throw (ex);
    }
};

OnePlayerCollector.prototype.listen = function() {

    this.video.addEventListener('loadedmetadata', this.onMetadataLoadedListener);
    this.video.addEventListener('ended', this.playbackEventListener);
    this.video.addEventListener('pause', this.playbackEventListener);
    this.video.addEventListener('playing', this.playbackEventListener);
    this.video.addEventListener('seeking', this.playbackEventListener);
    this.video.addEventListener('play', this.playbackEventListener);
    this.video.addEventListener('waiting', this.playbackEventListener);
    this.video.addEventListener('timeupdate', this.timeupdateListener);
    this.video.addEventListener('player_error', this.errorListener);

    this.player.addEventListener('qualityChangeRendered', this.qualityChangeRenderedListener);

};

OnePlayerCollector.prototype.reset = function() {

    this.video.removeEventListener('loadedmetadata', this.onMetadataLoadedListener);
    this.video.removeEventListener('ended', this.playbackEventListener);
    this.video.removeEventListener('pause', this.playbackEventListener);
    this.video.removeEventListener('playing', this.playbackEventListener);
    this.video.removeEventListener('seeking', this.playbackEventListener);
    this.video.removeEventListener('play', this.playbackEventListener);
    this.video.removeEventListener('waiting', this.playbackEventListener);
    this.video.removeEventListener('timeupdate', this.timeupdateListener);
    this.video.removeEventListener('player_error', this.errorListener);

    this.player.removeEventListener('qualityChangeRendered', this.qualityChangeRenderedListener);
};

OnePlayerCollector.prototype.addSession = function(stream) {
    var metric = new MetricsVo.Session();
    metric.id = this.sessionId;
    metric.playerid = this.playerId;
    metric.browserid = this.database.getBrowserId();
    metric.userAgent = navigator.userAgent;
    metric.uri = stream.url;
    metric.provider = document.location.href; //document.referrer;
    metric.loopMode = this.video.loop;
    metric.startTime = new Date().getTime();
    metric.endTime = null;
    metric.playerType = this.player.getVersion();
    metric.userData = stream.userData;

    this.database.addMetric({session: metric});
};

OnePlayerCollector.prototype.addMetadata = function(type) {

    var trackInfo = this.player.getSelectedTrackForType(type);

    var metric = new MetricsVo.MetaData();
    metric.contentType = trackInfo.mediaType;
    metric.codec = trackInfo.codec;
    metric.format = trackInfo.mimeType;
    metric.bitrates = trackInfo.bitrates;
    metric.duration = this.player.isLive() ? -1 : this.player.getDuration();

    this.database.addMetric({metadata: metric});
};

OnePlayerCollector.prototype.addEncoding = function(type, previousQuality, newQuality) {
    var trackInfo = this.player.getSelectedTrackForType(type);
    if (!trackInfo) {
        return;
    }
    var bitrates = trackInfo.bitrates;
    if (!bitrates) {
        return;
    }

    var metric = new MetricsVo.Encoding();
    metric.contentType = type;
    metric.bitrate = bitrates[newQuality];
    metric.position = this.video.currentTime;
    metric.previousBitrate = isNaN(previousQuality) ? 0 : bitrates[previousQuality].bitrate;

    this.database.addMetric({encoding: metric});
};

OnePlayerCollector.prototype.addState = function(state, reason) {
    // Ignore 'buffering' state in 'startup' state
    var currentState = this.database.getLastMetricValue('state');
    if (state === 'buffering' && (currentState.current == 'startup' || currentState.current == 'seeking')) {
        return;
    }

    var metric = new MetricsVo.State();
    metric.current = state;
    metric.position = this.video.currentTime;
    if (reason) {
        metric.reason = reason;
    }

    this.database.addMetric({state: metric});
};

OnePlayerCollector.prototype.addAction = function(action) {
    var session = this.database.getLastMetricValue('session');
    if (!session) {
        return;
    }

    var metric = new MetricsVo.Action();
    metric.type = action;
    metric.origin = session.currentPosition ? session.currentPosition : 0;
    metric.position = this.video.currentTime;

    this.database.addMetric({action: metric});
};

OnePlayerCollector.prototype.addCondition = function() {

    var hasWebKit = ("webkitDroppedFrameCount" in this.video),
        hasQuality = ("getVideoPlaybackQuality" in this.video),
        playbackQuality,
        metric = new MetricsVo.Condition();

    if (hasQuality) {
        playbackQuality = this.video.getVideoPlaybackQuality();
    }
    else if (hasWebKit) {
        playbackQuality = {
            droppedVideoFrames: this.video.webkitDroppedFrameCount,
            totalVideoFrames: this.video.webkitDecodedFrameCount
        };
    }

    metric.droppedFrames = playbackQuality.droppedFrames;
    metric.totalVideoFrames = playbackQuality.totalVideoFrames;
    metric.videoWidth = this.video.videoWidth;
    metric.videoHeight = this.video.videoHeight;
    metric.fullscreen = ((document.webkitIsFullScreen || document.msFullscreenElement || document.mozFullScreen) ? 1 : 0);
    metric.bandwidth = 0; // this.player.getAverageThroughput('video');

    this.database.addMetric({condition: metric});
};

OnePlayerCollector.prototype.onMetadataLoaded = function() {
    try {
        this.addMetadata('video');
    } catch (ex) {
        throw (ex);
    }
};

OnePlayerCollector.prototype.onPlaybackEvent = function(e) {
    try {
        var state = this.playbackStates[e.type],
            reason = (e.type === 'ended') ? 1 : undefined;
        if (state) {
            this.addState(state, reason);
        }
    
        var action = this.playbackActions[e.type];
        if (action) {
            this.addAction(action);
        }
    } catch (ex) {
        throw (ex);
    }
};

OnePlayerCollector.prototype.onTimeupdate = function() {
    try {
        var session = this.database.getLastMetricValue('session');
        if (session === null) {
            return;
        }

        // Update session current position
        session.currentPosition = this.video.currentTime;
        if (session.maxPosition === undefined || this.video.currentTime > session.maxPosition) {
            session.maxPosition = this.video.currentTime;
        }

        // Update session current state duration
        this.database.updateCurrentState();

        // Add condition metric
        this.addCondition();

    } catch(ex) {
        throw (ex);
    }
};

OnePlayerCollector.prototype.onQualityChangeRendered = function(event) {
    try {
        if (event.mediaType !== 'video' && event.mediaType !== 'audio') {
            return;
        }    
        this.addEncoding(event.mediaType, event.oldQuality, event.newQuality);
    } catch (ex) {
        throw (ex);
    }
};

OnePlayerCollector.prototype.onError = function(e) {
    try {
        var metric = new MetricsVo.Error();
        metric.message = e.detail.message;
        metric.position = this.video.currentTime;

        switch (e.detail.code) {
            case 3:
            case 4:
            case 5:
            case 11:
                metric.ocode = this.ORANGE_DRM_OR_DECODER_ERROR;
                break;

            case 1:
            case 2:
            case 20:
            case 21:
                metric.ocode = this.ORANGE_HTTP_ERROR;
                break;

            case 100:
            case 101:
            case 102:
            case 103:
            case 104:
            case 105:
            case 106:
            case 110:
            case 111:
            case 112:
            case 113:
            case 114:
            case 117:
            case 118:
                metric.ocode = this.ORANGE_DRM_OR_DECODER_ERROR;
                break;

            case 115:
                metric.ocode = this.ORANGE_DRM_INVALID_LICENSE;
                break;

            case 116:
                metric.ocode = this.ORANGE_DRM_NOMORE_INVALID_LICENSE;
                break;

            case 10:
            case 999:
                metric.ocode = this.ORANGE_UNDEFINED_PLAYER_ERROR;
                break;

            default:
                metric.ocode = this.ORANGE_UNDEFINED_PLAYER_ERROR;
                break;
        }
        metric.code = e.detail.code.toString();
        this.database.addMetric({error: metric});
    } catch (ex) {
        throw (ex);
    }
};

OnePlayerCollector.prototype.onTeardown = function() {
    try {
        var reason = this.database.getLastMetricValue('error') ? 2 : 0;
        this.addState('stopped', reason);
    } catch(ex) {
        throw (ex);
    }
};

OnePlayerCollector.prototype.ORANGE_STREAM_BROKEN_ERROR = 10;
OnePlayerCollector.prototype.ORANGE_HTTP_ERROR = 20;
OnePlayerCollector.prototype.ORANGE_STALLED_STREAM_ERROR = 30;
OnePlayerCollector.prototype.ORANGE_DRM_OR_DECODER_ERROR = 40;
OnePlayerCollector.prototype.ORANGE_DRM_NO_LICENSE = 41;
OnePlayerCollector.prototype.ORANGE_DRM_INVALID_LICENSE = 42;
OnePlayerCollector.prototype.ORANGE_DRM_NOMORE_INVALID_LICENSE = 43;
OnePlayerCollector.prototype.ORANGE_GENERIC_ERROR = 50;
OnePlayerCollector.prototype.ORANGE_UNDEFINED_PLAYER_ERROR = 99;
OnePlayerCollector.prototype.ORANGE_DEVICE_CRASH_ERROR = 100;

// needed to be referenced in MetricsAgent scope
MetricsAgent.collectors.OnePlayerCollector = OnePlayerCollector;



